export default {
	name: 'b-table',
	props: {
		columnsDefine: {
			type: Array,
			default: function() {
				return []
			}
		},
		datas: {
			type: Array,
			default: function() {
				return []
			}
		},
		stripe: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			
		}
	},
	methods: {
		// 判断是否显示斑马纹
		showStripe(index) {
			return index % 2 === 0 && this.stripe
		},
		// 行点击事件
		clickRow(row, index) {
			this.$emit('row-click', {row: row, index: index})
		}
	}
}
